<template>
  <div>
    <page-layout>
      <div class="content">
        <div class="table-operator" style="margin-bottom: 16px;">
          <a-row type="flex" justify="space-between">
            <a-col>
              <a-row type="flex" justify="space-between">
                <a-button icon="sync" :disabled="loading" @click="handleRefresh" style="margin-right: 10px;" title="刷新"></a-button>
                <a-button
                  type="primary"
                  style="margin-right: 10px;"
                  icon="plus"
                  @click="handleSingleMenuClick('create')"
                >
                  添加规则、
                </a-button>
                <a-button
                  type="danger"
                  style="margin-right: 10px;"
                  icon="delete"
                  :disabled="!selectedRowKeys.length"
                  @click="handleMultiMenuClick('multiDeleta')"
                >
                  删除规则
                </a-button>
              </a-row>
            </a-col>
            <a-col>
              <a-row type="flex" justify="space-between"></a-row>
            </a-col>
          </a-row>
        </div>
        <a-alert type="info" showIcon style="margin-bottom: 16px;">
          <div slot="message">
            已选择&nbsp;
            <a style="font-weight: 600">{{ selectedRowKeys.length }}</a>&nbsp;&nbsp;项
            <a style="margin-left: 24px" @click="handleClearSelected">清空</a>
          </div>
        </a-alert>
        <a-table
          :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onTableSelectChange}"
          :columns="columns"
          :rowKey="record => record.id"
          :dataSource="data"
          :pagination="pagination"
          :loading="loading"
          @change="handleTableChange"
        >
          <template slot="operation" slot-scope="text, record">
            <a-button
              type="danger"
              style="margin-right: 10px;"
              icon="delete"
              @click="handleSingleMenuClick('delete', record)"
            >
              删除规则
            </a-button>
          </template>
        </a-table>
      </div>
    </page-layout>

    <create-modal />
    <delete-modal />
    <multi-delete />
  </div>
</template>

<script>
import CreateModal from "./Modal/Create";
import DeleteModal from "./Modal/Delete";
import MultiDelete from "./Modal/MultiDelete";
import PageLayout from "@/components/Layout/PageLayout";

import rule from '@/i18n/zh/rule'

const statusDicts = rule.rule.status

import tablePageMixins from "@/mixins/tablePageMixins";

import { getRuleList as getList } from "@/api/security/index";
export default {
  mixins: [tablePageMixins],
  components: {
    CreateModal,
    MultiDelete,
    DeleteModal,
    PageLayout
  },

  data() {
    return {
      getList,
      module: "security",
      id: "rules",
      name: "管理安全组规则",
    };
  },
  beforeMount () {
    const secGroupId = this.$route.params.id
    this.payload = { secGroupId }
  },

  methods: {
    __handleTransformToZh (status) {
      return statusDicts[status.toLowerCase()] || status
    },
    handleParseData (data) {
      data.forEach(item => {
        // 端口范围
        const { portRangeMax, portRangeMin, protocol, portProtocol } = item;
        let portScope;
        if (portRangeMax === portRangeMin) {
          if (portRangeMax === '无') {
            portScope = '任何';
          } else {
            portScope = portRangeMax + (portProtocol !== '无' && protocol === "tcp" ? `(${portProtocol})` : '');
          }
        } else {
          portScope = portRangeMin + ' - ' + (portRangeMax === '无' ? 'None' : portRangeMax);
        };

        // 方向
        const direction_zh = this.__handleTransformToZh(item.direction);
        Object.assign(item, {
          portScope,
          direction_zh,
          protocol: item.protocol === '无' ? '任何' : item.protocol,
          remoteIpPrefix: item.remoteIpPrefix === '无' ? '任何' : item.remoteIpPrefix,
        })
      })
      return data;
    },
  }
};
</script>

<style scoped>
.ant-tabs-bar {
  margin: 0;
}
</style>
